<template>
	<div>
		<!---->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>

		<!---->
		<el-card>
			<!---->
			<el-row>
				<el-col>
					<el-button type="primary" @click="showAddCateDialog"></el-button>
				</el-col>
			</el-row>

			<!-- vue-table-with-tree-grid  -->
			<!--<tree-table class="treeTable" :data="cateList" :columns="columns" :selection-type="false" :expand-type="false"
			            :show-index="true" :border="true" :stripe="false">
				&lt;!&ndash;&ndash;&gt;
				<template slot="isok" slot-scope="scope">
					<i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: lightgreen"></i>
					<i class="el-icon-error" v-else style="color: red"></i>
				</template>
				&lt;!&ndash;&ndash;&gt;
				<template slot="order" slot-scope="scope">
					<el-tag size="mini" v-if="scope.row.cat_level===0"></el-tag>
					<el-tag size="mini" type="success" v-else-if="scope.row.cat_level===1"></el-tag>
					<el-tag size="mini" type="warning" v-else></el-tag>
				</template>
				&lt;!&ndash;&ndash;&gt;
				<template slot="opt" slot-scope="scope">
					<el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row)"></el-button>
					<el-popconfirm title="" icon="el-icon-delete" iconColor="red" @onConfirm="removeCateById(scope.row.cat_id)">
						<el-button size="mini" type="danger" icon="el-icon-delete" slot="reference"></el-button>
					</el-popconfirm>
				</template>
			</tree-table>-->

			<!-- Element  -->
			<el-table :data="cateList" row-key="cat_id" :tree-props="{children: 'children'}" :border="true" :stripe="true">
				<el-table-column label="" type="index" width="50"></el-table-column>
				<el-table-column label="" prop="cat_name"></el-table-column>
				<el-table-column label="">
					<template v-slot="scope">
						<i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: lightgreen"></i>
						<i class="el-icon-error" v-else style="color: red"></i>
					</template>
				</el-table-column>
				<el-table-column label="">
					<template v-slot="scope">
						<el-tag size="mini" v-if="scope.row.cat_level===0"></el-tag>
						<el-tag size="mini" type="success" v-else-if="scope.row.cat_level===1"></el-tag>
						<el-tag size="mini" type="warning" v-else></el-tag>
					</template>
				</el-table-column>
				<el-table-column label="">
					<template v-slot="scope">
						<el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row)"></el-button>
						<el-popconfirm title="" icon="el-icon-delete" iconColor="red" @onConfirm="removeCateById(scope.row.cat_id)">
							<el-button size="mini" type="danger" icon="el-icon-delete" slot="reference"></el-button>
						</el-popconfirm>
					</template>
				</el-table-column>
			</el-table>

			<!---->
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum"
			               :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize" :total="total"
			               layout="total, sizes, prev, pager, next, jumper" background>
			</el-pagination>
		</el-card>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="addCateDialogVisible" :close-on-click-modal="false" @close="addCateDialogClosed">
			<!---->
			<el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="80px">
				<el-form-item label="" prop="cat_name">
					<el-input v-model="addCateForm.cat_name"></el-input>
				</el-form-item>
				<el-form-item label="">
					<!-- options -->
					<!-- props -->
					<el-cascader v-model="selectedKeys" :options="parentCateList" :props="cascaderProps" @change="parentCateChange" :clearable="true"></el-cascader>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="addCateDialogVisible=false"> </el-button>
				<el-button type="primary" @click="addCate"> </el-button>
			</span>
		</el-dialog>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="editCateDialogVisible" :close-on-click-modal="false" @close="editCateDialogClosed">
			<!---->
			<el-form :model="editCateForm" :rules="editCateFormRules" ref="editCateFormRef" label-width="80px">
				<el-form-item label="" prop="cat_name">
					<el-input v-model="editCateForm.cat_name"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="editCateDialogVisible=false"> </el-button>
				<el-button type="primary" @click="editCate"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		name: "Cate",
		data() {
			return {
				//
				addCateDialogVisible: false,
				//
				addCateForm: {
					cat_name: '',
					//id
					cat_pid: 0,
					//
					cat_level: 0
				},
				addCateFormRules: {
					cat_name: [
						{required: true, message: '', trigger: 'blur'}
					]
				},
				//
				parentCateList: [],
				//
				cascaderProps: {
					expandTrigger: 'hover',
					//
					checkStrictly: true,
					value: 'cat_id',
					label: 'cat_name',
					children: 'children'
				},
				//id
				selectedKeys: [],
				//
				editCateDialogVisible: false,
				//
				editCateForm: {
					cat_id: 0,
					cat_name: ''
				},
				editCateFormRules: {
					cat_name: [
						{required: true, message: '', trigger: 'blur'}
					]
				},
				//
				queryInfo: {
					type: 3,
					pagenum: 1,//
					pagesize: 5//
				},
				//
				cateList: [],
				//
				total: 0,
				columns: [
					{
						label: '',
						prop: 'cat_name'
					},
					{
						label: '',
						//
						type: 'template',
						//
						template: 'isok'
					},
					{
						label: '',
						//
						type: 'template',
						//
						template: 'order'
					},
					{
						label: '',
						//
						type: 'template',
						//
						template: 'opt'
					}
				]
			}
		},
		created() {
			this.getCateList()
		},
		methods: {
			//
			getCateList() {
				axios.get('categories', {params: this.queryInfo}).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.cateList = res.data.result
						this.total = res.data.total
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			//pagesize
			handleSizeChange(newSize) {
				this.queryInfo.pagesize = newSize
				this.getCateList()
			},
			//pagenum
			handleCurrentChange(newPage) {
				this.queryInfo.pagenum = newPage
				this.getCateList()
			},
			//
			showAddCateDialog() {
				this.getParentCateList()
				this.addCateDialogVisible = true
			},
			//
			getParentCateList() {
				axios.get('categories', {params: {type: 2}}).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.parentCateList = res.data
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			//
			parentCateChange() {
				// selectedKeys  length 0 
				if (this.selectedKeys.length > 0) {
					//id
					this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
					//
					this.addCateForm.cat_level = this.selectedKeys.length
				} else {
					//id
					this.addCateForm.cat_pid = 0
					//
					this.addCateForm.cat_level = 0
				}
			},
			//
			addCateDialogClosed() {
				this.$refs.addCateFormRef.resetFields()
				this.selectedKeys = []
				this.addCateForm.cat_pid = 0
				this.addCateForm.cat_level = 0
			},
			//
			addCate() {
				this.$refs.addCateFormRef.validate(valid => {
					if (valid) {
						axios.post('categories', this.addCateForm).then(response => {
							const res = response.data
							if (res.meta.status === 201) {
								this.$message.success(res.meta.msg)
								this.getCateList()
								this.addCateDialogVisible = false
							} else {
								this.$message.error(res.meta.msg)
							}
						}).catch(() => {
							this.$message.error('')
						})
					}
				})
			},
			showEditDialog(cateInfo) {
				this.editCateForm.cat_id = cateInfo.cat_id
				this.editCateForm.cat_name = cateInfo.cat_name
				this.editCateDialogVisible = true
			},
			//
			editCateDialogClosed() {
				this.$refs.editCateFormRef.resetFields()
				this.editCateForm.cat_id = 0
				this.editCateForm.cat_name = ''
			},
			//
			editCate() {
				this.$refs.editCateFormRef.validate(valid => {
					if (valid) {
						axios.put(`categories/${this.editCateForm.cat_id}`, {cat_name: this.editCateForm.cat_name}).then(response => {
							const res = response.data
							if (res.meta.status === 200) {
								this.$message.success(res.meta.msg)
								this.getCateList()
								this.editCateDialogVisible = false
							} else {
								this.$message.error(res.meta.msg)
							}
						}).catch(() => {
							this.$message.error('')
						})
					}
				})
			},
			removeCateById(id) {
				axios.delete(`categories/${id}`).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.$message.success(res.meta.msg)
						this.getCateList()
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			}
		}
	}
</script>

<style scoped>
	.treeTable {
		margin-top: 15px;
		margin-bottom: 15px;
	}

	.el-cascader {
		width: 100%;
	}
	.el-button{
		margin-right: 10px;
	}
</style>