<template>
	<div>
		<!---->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>

		<el-card>
			<el-row>
				<el-col :span="6">
					<el-input placeholder="" v-model="queryInfo.query" :clearable="true" @clear="getOrderList">
						<el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
					</el-input>
				</el-col>
			</el-row>

			<!--table-->
			<el-table :data="orderList" border stripe>
				<el-table-column label="" type="index" width="50"></el-table-column>
				<el-table-column label="" prop="order_number"></el-table-column>
				<el-table-column label="" prop="order_price"></el-table-column>
				<el-table-column label="">
					<template v-slot="scope">
						<el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
						<el-tag type="danger" v-else></el-tag>
					</template>
				</el-table-column>
				<el-table-column label="" prop="is_send"></el-table-column>
				<el-table-column label="">
					<template v-slot="scope">{{ scope.row.create_time | dateFormat}}</template>
				</el-table-column>
				<el-table-column label="" width="200">
					<template v-slot="scope">
						<el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog"></el-button>
						<el-button size="mini" type="success" icon="el-icon-location" @click="showProgressDialog(scope.row)"></el-button>
					</template>
				</el-table-column>
			</el-table>

			<!---->
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum"
			               :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pagesize" :total="total"
			               layout="total, sizes, prev, pager, next, jumper" background>
			</el-pagination>
		</el-card>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="editDialogVisible" :close-on-click-modal="false" @close="editDialogClosed">
			<!---->
			<el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px">
				<el-form-item label="/" prop="address1">
					<el-cascader :options="citydata" v-model="editForm.address1"></el-cascader>
				</el-form-item>
				<el-form-item label="" prop="address2">
					<el-input v-model="editForm.address2"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="editDialogVisible=false"> </el-button>
				<el-button type="primary" @click="editDialogVisible=false"> </el-button>
			</span>
		</el-dialog>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="progressDialogVisible">
			<!---->
			<el-timeline>
				<el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
					{{activity.context}}
				</el-timeline-item>
			</el-timeline>
		</el-dialog>
	</div>
</template>

<script>
	import citydata from "./citydata";

	export default {
		name: "Order",
		data() {
			return {
				queryInfo: {
					query: '',
					pagenum: 2,
					pagesize: 10
				},
				orderList: [],
				total: 0,
				editDialogVisible: false,
				progressDialogVisible: false,
				editForm: {
					address1: [],
					address2: ''
				},
				editFormRules: {
					address1: [
						{required: true, message: '/', trigger: 'blur'},
					],
					address2: [
						{required: true, message: '', trigger: 'blur'},
					]
				},
				citydata,
				progressInfo: []
			}
		},
		created() {
			this.getOrderList()
		},
		methods: {
			getOrderList() {
				axios.get('orders', {params: this.queryInfo}).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.orderList = res.data.goods
						this.total = res.data.total
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			//pagesize
			handleSizeChange(newSize) {
				this.queryInfo.pagesize = newSize
				this.getOrderList()
			},
			//
			handleCurrentChange(newPage) {
				this.queryInfo.pagenum = newPage
				this.getOrderList()
			},
			showEditDialog() {
				this.editDialogVisible = true
			},
			editDialogClosed() {
				this.$refs.editFormRef.resetFields()
			},
			showProgressDialog() {
				axios.get('kuaidi/1106975712662').then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.progressInfo = res.data
						this.progressDialogVisible = true
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			}
		}
	}
</script>

<style lang="less" scoped>
	.el-cascader {
		width: 100%;
	}
</style>