<template>
	<div class="login_container">
		<div class="login_box">
			<!---->
			<div class="avatar_box">
				<img src="../assets/avatar.jpg" alt="">
			</div>
			<!---->
			<el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" class="login_form">
				<el-form-item prop="username">
					<el-input v-model="loginForm.username" prefix-icon="el-icon-user-solid"></el-input>
				</el-form-item>
				<el-form-item prop="password">
					<el-input v-model="loginForm.password" prefix-icon="el-icon-lock" show-password></el-input>
				</el-form-item>
				<el-form-item class="btns">
					<el-button type="primary" @click="login"></el-button>
					<el-button type="info" @click="resetLoginForm"></el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Login",
		data() {
			return {
				loginForm: {
					username: 'admin',
					password: '123456'
				},
				loginFormRules: {
					username: [
						{required: true, message: '', trigger: 'blur'},
					],
					password: [
						{required: true, message: '', trigger: 'blur'},
					]
				}
			}
		},
		methods: {
			resetLoginForm() {
				this.$refs.loginFormRef.resetFields();
			},
			login() {
				// this.$router.push('/home');
				this.$refs.loginFormRef.validate(valid => {
					if (valid) {
						axios.post("login", this.loginForm).then(response => {
							const res = response.data
							if (res.meta.status === 200) {
								this.$message.success(res.meta.msg);
								window.sessionStorage.setItem('token', res.data.token)
								this.$router.push('/home')
							} else {
								this.$message.error(res.meta.msg);
							}
						}).catch(() => {
							this.$message.error('')
						})
					}
				})
			}
		}
	}
</script>

<style lang="less" scoped>
	.login_container {
		height: 100%;
		background-color: #2b4b6b;
	}

	.login_box {
		width: 450px;
		height: 300px;
		background-color: #fff;
		border-radius: 3px;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);

		.avatar_box {
			height: 130px;
			width: 130px;
			border: 1px solid #eee;
			border-radius: 50%;
			padding: 10px;
			box-shadow: 0 0 10px #ddd;
			position: absolute;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: #fff;

			img {
				width: 100%;
				height: 100%;
				border-radius: 50%;
				background-color: #eee;
			}
		}
	}

	.login_form {
		position: absolute;
		bottom: 0;
		width: 100%;
		padding: 0 20px;
		box-sizing: border-box;
	}

	.btns {
		display: flex;
		justify-content: flex-end;
	}

</style>