<template>
	<div>
		<!---->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>

		<el-card>
			<!---->
			<el-alert title="" type="info" center show-icon :closable="false"></el-alert>
			<!---->
			<el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
				<el-step title=""></el-step>
				<el-step title=""></el-step>
				<el-step title=""></el-step>
				<el-step title=""></el-step>
				<el-step title=""></el-step>
				<el-step title=""></el-step>
			</el-steps>
			<!--tab-->
			<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px" label-position="top">
				<el-tabs v-model="activeIndex" tab-position="left" :before-leave="beforeTabLeave">
					<el-tab-pane label="" name="0">
						<el-form-item label="" prop="goods_name">
							<el-input v-model="addForm.goods_name"></el-input>
						</el-form-item>
						<el-form-item label="" prop="goods_price">
							<el-input v-model="addForm.goods_price" type="number"></el-input>
						</el-form-item>
						<el-form-item label="" prop="goods_weight">
							<el-input v-model="addForm.goods_weight" type="number"></el-input>
						</el-form-item>
						<el-form-item label="" prop="goods_number">
							<el-input v-model="addForm.goods_number" type="number"></el-input>
						</el-form-item>
						<el-form-item label="" prop="goods_cat">
							<el-cascader v-model="addForm.goods_cat" :options="cateList" :props="cascaderProps" @change="handleChange" style="width: 300px"></el-cascader>
						</el-form-item>
					</el-tab-pane>
					<el-tab-pane label="" name="1">
						<el-form-item :label="item.attr_name" v-for="(item,index) in manyTableData" :key="item.attr_id">
							<el-checkbox-group v-model="checkedManyTableData[index].attr_vals">
								<el-checkbox :label="cb" v-for="(cb,index) in item.attr_vals" :key="index" border></el-checkbox>
							</el-checkbox-group>
						</el-form-item>
					</el-tab-pane>
					<el-tab-pane label="" name="2">
						<el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
							<el-input v-model="item.attr_vals"></el-input>
						</el-form-item>
					</el-tab-pane>
					<el-tab-pane label="" name="3">
						<el-upload action="http://127.0.0.1:8888/api/private/v1/upload" :on-preview="handlePreview"
						           :on-remove="handleRemove" list-type="picture" :headers="headerObj" :on-success="handleSuccess">
							<el-button type="primary"></el-button>
						</el-upload>
					</el-tab-pane>
					<el-tab-pane label="" name="4">
						<quill-editor v-model="addForm.goods_introduce"></quill-editor>
						<el-button type="primary" class="btnAdd" @click="add"></el-button>
					</el-tab-pane>
				</el-tabs>
			</el-form>
		</el-card>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="previewDialogVisible">
			<!---->
			<img :src="previewPath" alt="" class="previewImg">
		</el-dialog>
	</div>
</template>

<script>
	import _ from 'lodash'

	export default {
		name: "Add",
		data() {
			return {
				cateList: [],
				//
				cascaderProps: {
					expandTrigger: 'hover',
					value: 'cat_id',
					label: 'cat_name',
					children: 'children'
				},
				activeIndex: '0',
				addForm: {
					goods_name: '',
					goods_price: 0,
					goods_weight: 0,
					goods_number: 0,
					goods_cat: [],
					pics: [],
					goods_introduce: '',
					attrs: []
				},
				addFormRules: {
					goods_name: [
						{required: true, message: '', trigger: 'blur'}
					],
					goods_price: [
						{required: true, message: '', trigger: 'blur'}
					],
					goods_weight: [
						{required: true, message: '', trigger: 'blur'}
					],
					goods_number: [
						{required: true, message: '', trigger: 'blur'}
					],
					goods_cat: [
						{required: true, message: '', trigger: 'blur'}
					]
				},
				//
				manyTableData: [],
				//
				checkedManyTableData: [],
				//
				onlyTableData: [],
				//
				headerObj: {
					Authorization: window.sessionStorage.getItem('token')
				},
				//url
				previewPath: '',
				//
				previewDialogVisible: false
			}
		},
		created() {
			this.getCateList()
		},
		methods: {
			//
			getCateList() {
				axios.get('categories').then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.cateList = res.data
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			handleChange() {
				if (this.addForm.goods_cat.length !== 3) {
					this.addForm.goods_cat = []
					return
				}
				//
				//
				axios.get(`categories/${this.cateId}/attributes`, {params: {sel: 'many'}}).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						res.data.forEach(item => {
							item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
						})
						this.manyTableData = res.data
						//manyTableData
						this.checkedManyTableData = _.cloneDeep(res.data)
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
				//
				axios.get(`categories/${this.cateId}/attributes`, {params: {sel: 'only'}}).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.onlyTableData = res.data
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			//tab
			beforeTabLeave(activeName, oldActiveName) {
				if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
					this.$message.error('')
					return false
				}
			},
			//
			handlePreview(file) {
				this.previewPath = file.response.data.url
				this.previewDialogVisible = true
			},
			//
			handleRemove(file) {
				const filePath = file.response.data.tmp_path
				const i = this.addForm.pics.findIndex(x => x.pic === filePath)
				this.addForm.pics.splice(i, 1)
			},
			//hook
			handleSuccess(response) {
				if (response.meta.status === 200) {
					this.$message.success(response.meta.msg)
					this.addForm.pics.push({pic: response.data.tmp_path})
				} else {
					this.$message.error(response.meta.msg)
				}
			},
			//
			add() {
				this.$refs.addFormRef.validate(valid => {
					if (valid) {
						//form
						const form = _.cloneDeep(this.addForm)
						form.goods_cat = form.goods_cat.join(',')
						//
						this.checkedManyTableData.forEach(item => {
							const newInfo = {
								attr_id: item.attr_id,
								attr_value: item.attr_vals.join(',')
							}
							form.attrs.push(newInfo)
						})
						//
						this.onlyTableData.forEach(item => {
							const newInfo = {
								attr_id: item.attr_id,
								attr_value: item.attr_vals
							}
							form.attrs.push(newInfo)
						})

						axios.post('goods', form).then(response => {
							const res = response.data
							if (res.meta.status === 201) {
								this.$message.success(res.meta.msg)
								this.$router.push('/goods')
							} else {
								this.$message.error(res.meta.msg)
							}
						}).catch(() => {
							this.$message.error('')
						})
					} else {
						return this.$message.error('')
					}
				})
			}
		},
		computed: {
			cateId() {
				if (this.addForm.goods_cat.length === 3) {
					return this.addForm.goods_cat[2]
				}
				return null
			}
		}
	}
</script>

<style lang="less" scoped>
	.el-steps .el-step {
		flex-basis: 16.6667% !important
	}

	.el-checkbox {
		margin: 0 10px 0 0 !important;
	}

	.previewImg {
		width: 100%;
	}

	.btnAdd {
		margin-top: 15px;
	}
</style>