<template>
	<div>
		<!---->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>

		<!---->
		<el-card>
			<!---->
			<el-table :data="rightsList" :border="true" :stripe="true">
				<el-table-column label="" type="index" width="50"></el-table-column>
				<el-table-column label="" prop="authName"></el-table-column>
				<el-table-column label="" prop="path"></el-table-column>
				<el-table-column label="" prop="level">
					<template v-slot="scope">
						<el-tag v-if="scope.row.level==='0'"></el-tag>
						<el-tag type="success" v-if="scope.row.level==='1'"></el-tag>
						<el-tag type="warning" v-if="scope.row.level==='2'"></el-tag>
					</template>
				</el-table-column>
			</el-table>
		</el-card>
	</div>
</template>

<script>
	export default {
		name: "Rights",
		data() {
			return {
				//
				rightsList: []
			}
		},
		created() {
			this.getRightsList()
		},
		methods: {
			//
			getRightsList() {
				axios.get('rights/list').then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.rightsList = res.data
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			}
		}
	}
</script>

