<template>
	<div>
		<!---->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>

		<!---->
		<el-card>
			<!---->
			<el-row :gutter="10">
				<el-col :span="6">
					<el-input placeholder="" v-model="queryInfo.query" :clearable="true" @clear="getUserList">
						<el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
					</el-input>
				</el-col>
				<el-col :span="6">
					<el-button type="primary" @click="addDialogVisible=true"></el-button>
				</el-col>
			</el-row>

			<!---->
			<el-table :data="userList" :border="true" :stripe="true">
				<el-table-column label="" type="index" width="50"></el-table-column>
				<el-table-column label="" prop="username"></el-table-column>
				<el-table-column label="" prop="email"></el-table-column>
				<el-table-column label="" prop="mobile"></el-table-column>
				<el-table-column label="" prop="role_name"></el-table-column>
				<el-table-column label="">
					<template v-slot="scope">
						<el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
					</template>
				</el-table-column>
				<el-table-column label="" width="200">
					<template v-slot="scope">
						<!---->
						<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
						<!---->
						<el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
						<!---->
						<el-tooltip effect="dark" content="" placement="top" :enterable="false">
							<el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetRoleDialog(scope.row)"></el-button>
						</el-tooltip>
					</template>
				</el-table-column>
			</el-table>

			<!---->
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum"
			               :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize" :total="total"
			               layout="total, sizes, prev, pager, next, jumper" background>
			</el-pagination>
		</el-card>


		<!---->
		<el-dialog title="" width="50%" :visible.sync="addDialogVisible" :close-on-click-modal="false" @close="addDialogClosed">
			<!---->
			<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
				<el-form-item label="" prop="username">
					<el-input v-model="addForm.username"></el-input>
				</el-form-item>
				<el-form-item label="" prop="password">
					<el-input v-model="addForm.password"></el-input>
				</el-form-item>
				<el-form-item label="" prop="email">
					<el-input v-model="addForm.email"></el-input>
				</el-form-item>
				<el-form-item label="" prop="mobile">
					<el-input v-model="addForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="addDialogVisible=false"> </el-button>
				<el-button type="primary" @click="addUser"> </el-button>
			</span>
		</el-dialog>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="editDialogVisible" :close-on-click-modal="false" @close="editDialogClosed">
			<!---->
			<el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
				<el-form-item label="" prop="username">
					<el-input v-model="editForm.username" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="" prop="email">
					<el-input v-model="editForm.email"></el-input>
				</el-form-item>
				<el-form-item label="" prop="mobile">
					<el-input v-model="editForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="editDialogVisible=false"> </el-button>
				<el-button type="primary" @click="editUser"> </el-button>
			</span>
		</el-dialog>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="setRoleDialogVisible" :close-on-click-modal="false" @close="setRoleDialogClosed">
			<!---->
			<div>
				<p>{{ userInfo.username }}</p>
				<p>{{ userInfo.role_name }}</p>
				<p>
					<el-select v-model="selectRoleId" placeholder="">
						<el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id"></el-option>
					</el-select>
				</p>
			</div>
			<!---->
			<span slot="footer">
				<el-button @click="setRoleDialogVisible=false"> </el-button>
				<el-button type="primary" @click="saveRoleInfo"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		name: "Users",
		data() {
			let checkEmail = (rule, value, callback) => {
				const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
				if (regEmail.test(value)) {
					return callback()
				}
				callback(new Error(''))
			}
			let checkMobile = (rule, value, callback) => {
				const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
				if (regMobile.test(value)) {
					return callback()
				}
				callback(new Error(''))
			}
			return {
				//
				queryInfo: {
					query: '',
					pagenum: 1,//
					pagesize: 5//
				},
				userList: [],
				total: 0,
				//
				addDialogVisible: false,
				//
				editDialogVisible: false,
				//
				setRoleDialogVisible: false,
				//
				addForm: {
					username: '',
					password: '',
					email: '',
					mobile: ''
				},
				//
				addFormRules: {
					username: [
						{required: true, message: '', trigger: 'blur'},
						{min: 3, max: 10, message: '3~10', trigger: 'blur'}
					],
					password: [
						{required: true, message: '', trigger: 'blur'},
						{min: 6, max: 15, message: '6~15', trigger: 'blur'}
					],
					email: [
						{required: true, message: '', trigger: 'blur'},
						{validator: checkEmail, trigger: 'blur'}
					],
					mobile: [
						{required: true, message: '', trigger: 'blur'},
						{validator: checkMobile, trigger: 'blur'}
					]
				},
				//
				editForm: {},
				//
				editFormRules: {
					email: [
						{required: true, message: '', trigger: 'blur'},
						{validator: checkEmail, trigger: 'blur'}
					],
					mobile: [
						{required: true, message: '', trigger: 'blur'},
						{validator: checkMobile, trigger: 'blur'}
					]
				},
				//
				userInfo: {},
				//
				rolesList: [],
				//id
				selectRoleId: ''
			}
		},
		created() {
			this.getUserList()
		},
		methods: {
			getUserList() {
				axios.get('users', {params: this.queryInfo}).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.userList = res.data.users
						this.total = res.data.total
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error("")
				})
			},
			//pagesize
			handleSizeChange(newSize) {
				this.queryInfo.pagesize = newSize
				this.getUserList()
			},
			//
			handleCurrentChange(newPage) {
				this.queryInfo.pagenum = newPage
				this.getUserList()
			},
			//switch
			userStateChanged(userInfo) {
				axios.put(`users/${userInfo.id}/state/${userInfo.mg_state}`).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.$message.success(res.meta.msg)
					} else {
						userInfo.mg_state = !userInfo.mg_state
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error("")
				})
			},
			//
			addDialogClosed() {
				this.$refs.addFormRef.resetFields()
			},
			//
			addUser() {
				this.$refs.addFormRef.validate(valid => {
					if (valid) {
						axios.post('users', this.addForm).then(response => {
							const res = response.data
							if (res.meta.status === 201) {
								//
								this.addDialogVisible = false
								this.getUserList()
								this.$message.success(res.meta.msg)
							} else {
								this.$message.error(res.meta.msg)
							}
						}).catch(() => {
							this.$message.error("")
						})
					}
				})
			},
			//
			showEditDialog(id) {
				axios.get(`users/${id}`).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.editForm = res.data
					} else {
						return this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					return this.$message.error("")
				})
				this.editDialogVisible = true
			},
			//
			editDialogClosed() {
				this.$refs.editFormRef.resetFields()
			},
			//
			editUser() {
				this.$refs.editFormRef.validate(valid => {
					if (valid) {
						axios.put(`users/${this.editForm.id}`, {email: this.editForm.email, mobile: this.editForm.mobile}).then(response => {
							const res = response.data
							if (res.meta.status === 200) {
								//
								this.editDialogVisible = false
								this.getUserList()
								this.$message.success(res.meta.msg)
							} else {
								this.$message.error(res.meta.msg)
							}
						}).catch(() => {
							this.$message.error("")
						})
					}
				})
			},
			//id
			removeUserById(id) {
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					axios.delete(`users/${id}`).then(response => {
						const res = response.data
						if (res.meta.status === 200) {
							this.$message.success(res.meta.msg)
							this.getUserList()
						} else {
							this.$message.error(res.meta.msg)
						}
					}).catch(() => {
						this.$message.error('')
					})
				}).catch(() => {
					this.$message.info('')
				});
			},
			//
			showSetRoleDialog(userInfo) {
				this.userInfo = userInfo
				axios.get('roles').then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.rolesList = res.data
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
				this.setRoleDialogVisible = true
			},
			//
			setRoleDialogClosed() {
				this.selectRoleId = ''
				this.userInfo = {}
			},
			//
			saveRoleInfo() {
				if (!this.selectRoleId) {
					return this.$message.error('')
				}
				axios.put(`users/${this.userInfo.id}/role`, {rid: this.selectRoleId}).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.getUserList()
						this.setRoleDialogVisible = false
						this.$message.success(res.meta.msg)
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			}
		}
	}
</script>

<style lang="less" scoped>

</style>