<template>
	<div>
		<!---->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>

		<el-card>
			<!---->
			<el-alert title="" type="info" center show-icon :closable="false"></el-alert>
			<!---->
			<el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
				<el-step title=""></el-step>
				<el-step title=""></el-step>
				<el-step title=""></el-step>
				<el-step title=""></el-step>
				<el-step title=""></el-step>
				<el-step title=""></el-step>
			</el-steps>
			<!--tab-->
			<el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px" label-position="top">
				<el-tabs v-model="activeIndex" tab-position="left">
					<el-tab-pane label="" name="0">
						<el-form-item label="" prop="goods_name">
							<el-input v-model="editForm.goods_name"></el-input>
						</el-form-item>
						<el-form-item label="" prop="goods_price">
							<el-input v-model="editForm.goods_price" type="number"></el-input>
						</el-form-item>
						<el-form-item label="" prop="goods_weight">
							<el-input v-model="editForm.goods_weight" type="number"></el-input>
						</el-form-item>
						<el-form-item label="" prop="goods_number">
							<el-input v-model="editForm.goods_number" type="number"></el-input>
						</el-form-item>
						<el-form-item label="">
							<el-cascader v-model="editForm.goods_cat" :options="cateList" :props="cascaderProps" style="width: 300px" disabled></el-cascader>
						</el-form-item>
					</el-tab-pane>
					<el-tab-pane label="" name="1">
						<el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
							<el-checkbox-group v-model="item.attr_value">
								<el-checkbox :label="cb" v-for="(cb,index) in item.attr_vals" :key="index" border></el-checkbox>
							</el-checkbox-group>
						</el-form-item>
					</el-tab-pane>
					<el-tab-pane label="" name="2">
						<el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
							<el-input v-model="item.attr_vals"></el-input>
						</el-form-item>
					</el-tab-pane>
					<el-tab-pane label="" name="3">
						<el-upload action="http://127.0.0.1:8888/api/private/v1/upload" :on-preview="handlePreview" :file-list="imgList"
						           :on-remove="handleRemove" list-type="picture" :headers="headerObj" :on-success="handleSuccess">
							<el-button type="primary"></el-button>
						</el-upload>
					</el-tab-pane>
					<el-tab-pane label="" name="4">
						<quill-editor v-model="editForm.goods_introduce"></quill-editor>
						<el-button type="primary" class="btnEdit" @click="postEdit"></el-button>
					</el-tab-pane>
				</el-tabs>
			</el-form>
		</el-card>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="previewDialogVisible">
			<!---->
			<img :src="previewPath" alt="" class="previewImg">
		</el-dialog>
	</div>
</template>

<script>
	export default {
		name: "Edit",
		data() {
			return {
				cateList: [],
				//
				cascaderProps: {
					expandTrigger: 'hover',
					value: 'cat_id',
					label: 'cat_name',
					children: 'children'
				},
				activeIndex: '0',
				editForm: {
					goods_name: '',
					goods_price: 0,
					goods_weight: 0,
					goods_number: 0,
					goods_cat: [],
					pics: [],
					goods_introduce: '',
					attrs: []
				},
				editFormRules: {
					goods_name: [
						{required: true, message: '', trigger: 'blur'}
					],
					goods_price: [
						{required: true, message: '', trigger: 'blur'}
					],
					goods_weight: [
						{required: true, message: '', trigger: 'blur'}
					],
					goods_number: [
						{required: true, message: '', trigger: 'blur'}
					]
				},
				//
				manyTableData: [],
				//
				onlyTableData: [],
				//
				imgList: [],
				//
				tmpImgList: [],
				//
				headerObj: {
					Authorization: window.sessionStorage.getItem('token')
				},
				//url
				previewPath: '',
				//
				previewDialogVisible: false
			}
		},
		created() {
			this.getCateList()
			this.getGoodsInfo()
		},
		methods: {
			//
			getGoodsInfo() {
				axios.get(`goods/${this.$route.params.id}`).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.editForm = res.data
						// split map  ['1','3','6'] => [1,3,6]
						this.editForm.goods_cat = this.editForm.goods_cat.split(',').map(Number)
						this.getAttrData()
						this.getImgList()
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error("")
				})
			},
			//
			getCateList() {
				axios.get('categories').then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.cateList = res.data
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			getAttrData() {
				//
				this.editForm.attrs.forEach(item => {
					if (item.attr_sel === 'many') {
						//
						item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
						//
						item.attr_value = item.attr_value ? item.attr_value.split(',') : []
						this.manyTableData.push(item)
					} else if (item.attr_sel === 'only') {
						this.onlyTableData.push(item)
					}
				})
			},
			getImgList() {
				this.editForm.pics.forEach(item => {
					this.imgList.push({
						name: item.pics_id,
						url: item.pics_big_url,
						pic: item.pics_big
					})
				})
			},
			//
			handlePreview(file) {
				this.previewPath = file.url
				this.previewDialogVisible = true
			},
			//
			handleRemove(file) {
				if (file.response) {
					const filePath = file.response.data.tmp_path
					const i = this.tmpImgList.findIndex(x => x.pic === filePath)
					this.tmpImgList.splice(i, 1)
				} else {
					const filePath = file.pic
					const i = this.imgList.findIndex(x => x.pic === filePath)
					this.imgList.splice(i, 1)
				}
			},
			//hook
			handleSuccess(response) {
				if (response.meta.status === 200) {
					this.$message.success(response.meta.msg)
					this.tmpImgList.push({pic: response.data.tmp_path})
				} else {
					this.$message.error(response.meta.msg)
				}
			},
			//
			postEdit() {
				this.$refs.editFormRef.validate(valid => {
					if (valid) {
						/*let pics = []
						this.imgList.forEach(item => {
							pics.push({pic: item.pic})
						})
						pics.push(...this.tmpImgList)*/

						const form = {
							goods_name: this.editForm.goods_name,
							goods_price: this.editForm.goods_price,
							goods_number: this.editForm.goods_number,
							goods_weight: this.editForm.goods_weight,
							goods_introduce: this.editForm.goods_introduce,
							// pics: pics,
							attrs: [],
							goods_cat: ''
						}
						form.goods_cat = this.editForm.goods_cat.join(',')
						//
						this.manyTableData.forEach(item => {
							const newInfo = {
								attr_id: item.attr_id,
								attr_value: item.attr_value.join(',')
							}
							form.attrs.push(newInfo)
						})
						//
						this.onlyTableData.forEach(item => {
							const newInfo = {
								attr_id: item.attr_id,
								attr_value: item.attr_vals
							}
							form.attrs.push(newInfo)
						})

						axios.put(`goods/${this.editForm.goods_id}`, form).then(response => {
							const res = response.data
							if (res.meta.status === 200) {
								this.$message.success(res.meta.msg)
								this.$router.push('/goods')
							} else {
								this.$message.error(res.meta.msg)
							}
						}).catch(() => {
							this.$message.error('')
						})
					} else {
						return this.$message.error('')
					}
				})
			}
		}
	}
</script>

<style lang="less" scoped>
	.el-steps .el-step {
		flex-basis: 16.6667% !important
	}

	.el-checkbox {
		margin: 0 10px 0 0 !important;
	}

	.previewImg {
		width: 100%;
	}

	.btnEdit {
		margin-top: 15px;
	}
</style>