<template>
	<div>
		<!---->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>

		<el-card>
			<!---->
			<el-alert title="" type="warning" :closable="false" show-icon></el-alert>
			<!---->
			<el-row class="cat_opt">
				<el-col>
					<span></span>
					<el-cascader v-model="selectedKeys" :options="cateList" :props="cascaderProps" @change="getParamsData" style="width: 300px"></el-cascader>
				</el-col>
			</el-row>
			<!-- tab -->
			<el-tabs v-model="activeName" @tab-click="getParamsData">
				<el-tab-pane label="" name="many">
					<el-button type="primary" :disabled="isBtnDisabled" @click="addDialogVisible=true"></el-button>
					<el-table :data="manyTableData" border stripe>
						<el-table-column type="expand">
							<template v-slot="scope">
								<!--tag-->
								<el-tag v-for="(item,index) in scope.row.attr_vals" :key="index" closable @close="handleTagClose(scope.row,index)">{{ item }}</el-tag>
								<!--tag-->
								<el-input class="input-new-tag" v-if="scope.row.inputTagVisible" v-model="scope.row.inputTagValue" ref="saveTagInput"
								          size="small" @keyup.enter.native="handleInputTagConfirm(scope.row)" @blur="handleInputTagConfirm(scope.row)"></el-input>
								<el-button v-else class="button-new-tag" size="small" @click="showTagInput(scope.row)">+ New Tag</el-button>
							</template>
						</el-table-column>
						<el-table-column label="" type="index" width="50"></el-table-column>
						<el-table-column label="" prop="attr_name"></el-table-column>
						<el-table-column label="">
							<template v-slot="scope">
								<el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row)"></el-button>
								<el-popconfirm title="" icon="el-icon-delete" iconColor="red" @onConfirm="removeParamsById(scope.row.attr_id)">
									<el-button size="mini" type="danger" icon="el-icon-delete" slot="reference"></el-button>
								</el-popconfirm>
							</template>
						</el-table-column>
					</el-table>
				</el-tab-pane>
				<el-tab-pane label="" name="only">
					<el-button type="primary" :disabled="isBtnDisabled" @click="addDialogVisible=true"></el-button>
					<el-table :data="onlyTableData" border stripe>
						<el-table-column type="expand">
							<template v-slot="scope">
								<!--tag-->
								<el-tag v-for="(item,index) in scope.row.attr_vals" :key="index" closable @close="handleTagClose(scope.row,index)">{{ item }}</el-tag>
								<!--tag-->
								<el-input class="input-new-tag" v-if="scope.row.inputTagVisible" v-model="scope.row.inputTagValue" ref="saveTagInput"
								          size="small" @keyup.enter.native="handleInputTagConfirm(scope.row)" @blur="handleInputTagConfirm(scope.row)"></el-input>
								<el-button v-else class="button-new-tag" size="small" @click="showTagInput(scope.row)">+ New Tag</el-button>
							</template>
						</el-table-column>
						<el-table-column label="" type="index" width="50"></el-table-column>
						<el-table-column label="" prop="attr_name"></el-table-column>
						<el-table-column label="">
							<template v-slot="scope">
								<el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row)"></el-button>
								<el-popconfirm title="" icon="el-icon-delete" iconColor="red" @onConfirm="removeParamsById(scope.row.attr_id)">
									<el-button size="mini" type="danger" icon="el-icon-delete" slot="reference"></el-button>
								</el-popconfirm>
							</template>
						</el-table-column>
					</el-table>
				</el-tab-pane>
			</el-tabs>
		</el-card>

		<!---->
		<el-dialog :title="''+titleText" width="50%" :visible.sync="addDialogVisible" :close-on-click-modal="false" @close="addDialogClosed">
			<!---->
			<el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
				<el-form-item :label="titleText" prop="attr_name">
					<el-input v-model="addForm.attr_name"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="addDialogVisible=false"> </el-button>
				<el-button type="primary" @click="addParams"> </el-button>
			</span>
		</el-dialog>

		<!---->
		<el-dialog :title="''+titleText" width="50%" :visible.sync="editDialogVisible" :close-on-click-modal="false" @close="editDialogClosed">
			<!---->
			<el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="80px">
				<el-form-item :label="titleText" prop="attr_name">
					<el-input v-model="editForm.attr_name"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="editDialogVisible=false"> </el-button>
				<el-button type="primary" @click="editParams"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		name: "Params",
		data() {
			return {
				//
				cateList: [],
				//
				cascaderProps: {
					expandTrigger: 'hover',
					value: 'cat_id',
					label: 'cat_name',
					children: 'children'
				},
				//
				selectedKeys: [],
				//tab
				activeName: 'many',
				//
				manyTableData: [],
				//
				onlyTableData: [],
				//
				addDialogVisible: false,
				addForm: {
					attr_name: ''
				},
				addFormRules: {
					attr_name: [
						{required: true, message: '', trigger: 'blur'}
					]
				},
				editDialogVisible: false,
				editForm: {},
				editFormRules: {
					attr_name: [
						{required: true, message: '', trigger: 'blur'}
					]
				},
			}
		},
		created() {
			this.getCateList()
		},
		methods: {
			//
			getCateList() {
				axios.get('categories').then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.cateList = res.data
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			//
			getParamsData() {
				//
				if (this.selectedKeys.length === 3) {
					//id
					axios.get(`categories/${this.cateId}/attributes`, {params: {sel: this.activeName}}).then(response => {
						const res = response.data
						res.data.forEach(item => {
							item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
							//tag
							item.inputTagVisible = false
							//tag
							item.inputTagValue = ''
						})
						if (res.meta.status === 200) {
							if (this.activeName === 'many') {
								this.manyTableData = res.data
							} else if (this.activeName === 'only') {
								this.onlyTableData = res.data
							}
						} else {
							this.$message.error(res.meta.msg)
						}
					}).catch(() => {
						this.$message.error('')
					})
				} else {
					this.selectedKeys = []
					this.manyTableData = []
					this.onlyTableData = []
				}
			},
			addDialogClosed() {
				this.$refs.addFormRef.resetFields()
			},
			//
			addParams() {
				this.$refs.addFormRef.validate(valid => {
					if (valid) {
						axios.post(`categories/${this.cateId}/attributes`, {
							attr_name: this.addForm.attr_name,
							attr_sel: this.activeName
						}).then(response => {
							const res = response.data
							if (res.meta.status === 201) {
								this.$message.success(res.meta.msg)
								this.getParamsData()
								this.addDialogVisible = false
							} else {
								this.$message.error(res.meta.msg)
							}
						}).catch(() => {
							this.$message.error('')
						})
					}
				})
			},
			showEditDialog(paramsInfo) {
				//paramsInfo
				//
				this.editForm = {...paramsInfo}
				this.editDialogVisible = true
			},
			editDialogClosed() {
				this.$refs.editFormRef.resetFields()
			},
			editParams() {
				this.$refs.editFormRef.validate(valid => {
					if (valid) {
						axios.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`, {
							attr_name: this.editForm.attr_name,
							attr_sel: this.activeName
						}).then(response => {
							const res = response.data
							if (res.meta.status === 200) {
								this.$message.success(res.meta.msg)
								this.getParamsData()
								this.editDialogVisible = false
							} else {
								this.$message.error(res.meta.msg)
							}
						}).catch(() => {
							this.$message.error('')
						})
					}
				})
			},
			removeParamsById(id) {
				axios.delete(`categories/${this.cateId}/attributes/${id}`).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.$message.success(res.meta.msg)
						this.getParamsData()
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			//tag
			handleInputTagConfirm(row) {
				if (row.inputTagValue.trim().length !== 0) {
					//tag push
					row.attr_vals.push(row.inputTagValue.trim())
					//put
					this.saveAttrVals(row)
				}
				row.inputTagValue = ''
				row.inputTagVisible = false
			},
			// attr_vals 
			saveAttrVals(row) {
				axios.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
					attr_name: row.attr_name,
					attr_sel: row.attr_sel,
					attr_vals: row.attr_vals.join(',')
				}).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.$message.success(res.meta.msg)
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			//tag
			showTagInput(row) {
				row.inputTagVisible = true
				//
				//$nextTick
				//row.inputTagVisible = truetaginputthis.$refs.saveTagInput.$refs.input
				this.$nextTick(_ => {
					this.$refs.saveTagInput.$refs.input.focus();
				});
			},
			//tag
			handleTagClose(row, index) {
				row.attr_vals.splice(index, 1)
				//put
				this.saveAttrVals(row)
			}
		},
		computed: {
			isBtnDisabled() {
				return this.selectedKeys.length !== 3;
			},
			//id
			cateId() {
				if (this.selectedKeys.length === 3) {
					return this.selectedKeys[2]
				}
				return null
			},
			//
			titleText() {
				if (this.activeName === 'many') {
					return ''
				} else {
					return ''
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	.cat_opt {
		margin: 15px 0;
	}

	.el-button {
		margin-right: 10px;
	}

	.el-tag {
		margin: 10px;
	}

	.input-new-tag {
		width: 120px;
	}

	.button-new-tag {
		width: 120px;
	}
</style>