<template>
	<div>
		<!---->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>

		<!---->
		<el-card>
			<!---->
			<el-row>
				<el-col>
					<el-button type="primary" @click="addRoleDialogVisible=true"></el-button>
				</el-col>
			</el-row>

			<!---->
			<el-table :data="roleList" :border="true" :stripe="true">
				<el-table-column type="expand" width="50">
					<template v-slot="scope">
						<el-row :class="['bdbottom',index1===0?'bdtop':'','vcenter']" v-for="(item1,index1) in scope.row.children" :key="item1.id">
							<!--  -->
							<el-col :span="5">
								<el-tag :closable="true" @close="removeRightById(scope.row,item1.id)">{{ item1.authName }}</el-tag>
								<i class="el-icon-caret-right"></i>
							</el-col>
							<!--  -->
							<el-col :span="19">
								<el-row :class="[index2!==0?'bdtop':'','vcenter']" v-for="(item2,index2) in item1.children" :key="item2.id">
									<el-col :span="6">
										<el-tag type="success" :closable="true" @close="removeRightById(scope.row,item2.id)">{{ item2.authName }}</el-tag>
										<i class="el-icon-caret-right"></i>
									</el-col>
									<el-col :span="18">
										<el-tag type="warning" v-for="(item3) in item2.children" :key="item3.id" :closable="true" @close="removeRightById(scope.row,item3.id)">
											{{ item3.authName }}
										</el-tag>
									</el-col>
								</el-row>
							</el-col>
						</el-row>
					</template>
				</el-table-column>
				<el-table-column label="" type="index" width="50"></el-table-column>
				<el-table-column label="" prop="roleName"></el-table-column>
				<el-table-column label="" prop="roleDesc"></el-table-column>
				<el-table-column label="" width="300">
					<template v-slot="scope">
						<el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)"></el-button>
						<el-button size="mini" type="danger" icon="el-icon-delete" @click="removeRoleById(scope.row.id)"></el-button>
						<el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-card>

		<!---->
		<el-dialog title="" width="50%" :visible.sync="addRoleDialogVisible" :close-on-click-modal="false" @close="addRoleDialogClosed">
			<!---->
			<el-form :model="addRoleForm" :rules="addRoleFormRules" ref="addRoleFormRef" label-width="80px">
				<el-form-item label="" prop="roleName">
					<el-input v-model="addRoleForm.roleName"></el-input>
				</el-form-item>
				<el-form-item label="" prop="roleDesc">
					<el-input v-model="addRoleForm.roleDesc"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="addRoleDialogVisible=false"> </el-button>
				<el-button type="primary" @click="addRole"> </el-button>
			</span>
		</el-dialog>
		
		<!---->
		<el-dialog title="" width="50%" :visible.sync="editRoleDialogVisible" :close-on-click-modal="false" @close="editRoleDialogClosed">
			<!---->
			<el-form :model="editRoleForm" :rules="editRoleFormRules" ref="editRoleFormRef" label-width="80px">
				<el-form-item label="" prop="roleName">
					<el-input v-model="editRoleForm.roleName"></el-input>
				</el-form-item>
				<el-form-item label="" prop="roleDesc">
					<el-input v-model="editRoleForm.roleDesc"></el-input>
				</el-form-item>
			</el-form>
			<!---->
			<span slot="footer">
				<el-button @click="editRoleDialogVisible=false"> </el-button>
				<el-button type="primary" @click="editRole"> </el-button>
			</span>
		</el-dialog>

		<!---->
		<el-dialog title="" width="30%" :visible.sync="setRightDialogVisible" :close-on-click-modal="false" @close="setRightsDialogClosed">
			<!---->
			<el-tree :data="rightsList" :props="treeProps" ref="treeRef" :show-checkbox="true" :default-expand-all="true"
			         node-key="id" :default-checked-keys="defaultKeys"></el-tree>
			<!---->
			<span slot="footer">
				<el-button @click="setRightDialogVisible=false"> </el-button>
				<el-button type="primary" @click="allotRights"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		name: "Roles",
		data() {
			return {
				//
				roleList: [],
				//
				setRightDialogVisible: false,
				//
				rightsList: [],
				//
				treeProps: {
					label: 'authName',
					children: 'children'
				},
				//id
				defaultKeys: [],
				//id
				roleId: '',
				//
				addRoleDialogVisible: false,
				//
				editRoleDialogVisible: false,
				//
				addRoleForm: {
					roleName: '',
					roleDesc: '',
				},
				//
				addRoleFormRules: {
					roleName: [
						{required: true, message: '', trigger: 'blur'},
					],
					roleDesc: [
						{required: true, message: '', trigger: 'blur'},
					]
				},
				//
				editRoleForm: {
					roleName: '',
					roleDesc: '',
				},
				//
				editRoleFormRules: {
					roleName: [
						{required: true, message: '', trigger: 'blur'},
					],
					roleDesc: [
						{required: true, message: '', trigger: 'blur'},
					]
				},
			}
		},
		created() {
			this.getRolesList()
		},
		methods: {
			getRolesList() {
				axios.get('roles').then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.roleList = res.data
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			//id
			removeRightById(role, rightId) {
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					axios.delete(`roles/${role.id}/rights/${rightId}`).then(response => {
						const res = response.data
						if (res.meta.status === 200) {
							role.children = res.data
							this.$message.success(res.meta.msg)
						} else {
							this.$message.error(res.meta.msg)
						}
					}).catch(() => {
						this.$message.error('')
					})
				}).catch(() => {
					this.$message.info('')
				});
			},
			//
			showSetRightDialog(role) {
				this.roleId = role.id
				axios.get('rights/tree').then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.rightsList = res.data
						this.getLeafKeys(role, this.defaultKeys)
						this.setRightDialogVisible = true
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			// id
			getLeafKeys(node, arr) {
				//nodechildren 
				if (!node.children) {
					return arr.push(node.id)
				}
				node.children.forEach(item => this.getLeafKeys(item, arr))
			},
			//
			setRightsDialogClosed() {
				this.defaultKeys = []
			},
			//
			allotRights() {
				const keys = [
					...this.$refs.treeRef.getCheckedKeys(),
					...this.$refs.treeRef.getHalfCheckedKeys()
				]
				const idStr = keys.join(',')
				axios.post(`roles/${this.roleId}/rights`, {rids: idStr}).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.getRolesList()
						this.setRightDialogVisible = false
						this.$message.success(res.meta.msg)
					} else {
						this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					this.$message.error('')
				})
			},
			//
			addRoleDialogClosed() {
				this.$refs.addRoleFormRef.resetFields()
			},
			//
			addRole(){
				this.$refs.addRoleFormRef.validate(valid => {
					if (valid) {
						axios.post('roles', this.addRoleForm).then(response => {
							const res = response.data
							if (res.meta.status === 201) {
								//
								this.addRoleDialogVisible = false
								this.getRolesList()
								this.$message.success(res.meta.msg)
							} else {
								this.$message.error(res.meta.msg)
							}
						}).catch(() => {
							this.$message.error("")
						})
					}
				})
			},
			showEditDialog(id){
				axios.get(`roles/${id}`).then(response => {
					const res = response.data
					if (res.meta.status === 200) {
						this.editRoleForm = res.data
					} else {
						return this.$message.error(res.meta.msg)
					}
				}).catch(() => {
					return this.$message.error("")
				})
				this.editRoleDialogVisible = true
			},
			//
			editRoleDialogClosed() {
				this.$refs.editRoleFormRef.resetFields()
			},
			//
			editRole(){
				this.$refs.editRoleFormRef.validate(valid => {
					if (valid) {
						axios.put(`roles/${this.editRoleForm.roleId}`, this.editRoleForm).then(response => {
							const res = response.data
							if (res.meta.status === 200) {
								//
								this.editRoleDialogVisible = false
								this.getRolesList()
								this.$message.success(res.meta.msg)
							} else {
								this.$message.error(res.meta.msg)
							}
						}).catch(() => {
							this.$message.error("")
						})
					}
				})
			},
			//id
			removeRoleById(id) {
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					axios.delete(`roles/${id}`).then(response => {
						const res = response.data
						if (res.meta.status === 200) {
							this.$message.success(res.meta.msg)
							this.getRolesList()
						} else {
							this.$message.error(res.meta.msg)
						}
					}).catch(() => {
						this.$message.error('')
					})
				}).catch(() => {
					this.$message.info('')
				});
			},
		}
	}
</script>

<style lang="less" scoped>
	.el-tag {
		margin: 10px;
	}

	.bdtop {
		border-top: 1px solid #eeeeee;
	}

	.bdbottom {
		border-bottom: 1px solid #eeeeee;
	}

	.vcenter {
		display: flex;
		align-items: center;
	}
</style>